.gen {
	padding: 20px 0;
	@include sm {
		padding: 70px 0; }
	&__col {
		&:not(:last-child) {
			@include sm {
				margin-bottom: 70px; } }
		&.is-active {
			.gen__footer,
			.gen__bg {
				opacity: 1;
				visibility: visible; } } }
	&__container {
		position: relative;
		padding: 50px 20px 58px;
		height: 100%;
		justify-content: space-between;
		flex-direction: column;
		display: flex;
		@include sm {
			padding: 0; }
		@include hover {
			.gen__footer,
			.gen__bg {
				opacity: 1;
				visibility: visible; } } }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 100%;
		background-color: $blue;
		opacity: 0;
		visibility: hidden;
		@include tr(opacity,visibility);
		@include md {
			display: none; }
		img {
			width: 100%;
			height: 100%;
			opacity: .1;
			object-fit: cover;
			object-position: center;
			display: block; } }
	&__wrap {
		position: relative;
		z-index: {}
		@include sm {
			@include clr; } }
	&__icon {
		margin-bottom: 30px;
		min-height: 60px;
		align-items: center;
		display: flex;
		@include sm {
			float: left;
			margin-right: 15px;
			width: 64px;
			clear: right; } }
	&__titlexs {
		margin-bottom: 30px;
		letter-spacing: -.018em;
		@include sm {
			margin-bottom: 10px;
			br {
				display: none; } } }
	&__text {
		@include sm {
			margin-left: 79px; } }
	&__footer {
		position: relative;
		z-index: 1;
		margin-top: 62px;
		opacity: 0;
		visibility: hidden;
		@include tr(opacity,visibility);
		@include md {
			visibility: visible;
			opacity: 1; }
		@include sm {
			margin-top: 30px;
			padding-left: 79px; } } }
