.hero {
	position: relative;
	padding-top: 80px;
	margin: 0 auto;
	@include md {
		padding-top: 100px; }
	&__slider {
		opacity: 0;
		visibility: hidden;
		&.slick-initialized {
			opacity: 1;
			visibility: visible; }
		.slick-dots {
			position: relative;
			margin: -124px auto 0;
			padding-bottom: 101px;
			max-width: 732px;
			z-index: 3;
			flex-wrap: wrap;
			align-items: center;
			display: flex;
			@include md {
				margin: 0;
				padding: 30px 37px;
				max-width: 100%;
				justify-content: center; }
			li {
				&.slick-active {
					button {
						border-color: $white;
						&:before {
							background-color: $white; } } }
				&:not(:last-child) {
					margin-right: 14px; } }
			button {
				position: relative;
				font-size: 0;
				@include circle(23);
				border: 1px solid transparent;
				@include tr(border-color);
				background-color: transparent;
				display: block;
				@include hover {
					border-color: $white;
					&:before {
						background-color: $white; } }
				&:before {
					position: absolute;
					content: '';
					top: 8px;
					left: 8px;
					@include circle(5);
					@include tr(background-color);
					background-color: rgba($white, .5);
					li.slick-active & {
						background-color: $white; } } } } }
	&__slide {
		position: relative;
		padding: 230px 0 170px;
		min-height: 800px;
		@include md {
			padding: 60px 0 15px;
			display: flex !important;
			min-height: 600px;
			align-items: center; }
		@include sm {
			min-height: 370px; } }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: .5;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			display: block; } }
	&__inner-sm {
		position: relative;
		margin: 0 auto;
		padding: 0;
		width: 100%;
		max-width: 732px;
		z-index: 1; }
	&__titlelg {
		margin-bottom: 60px;
		@include md {
			margin-bottom: 30px; } }
	&__bottom {
		position: absolute;
		bottom: 100px;
		left: 0;
		width: 100%;
		z-index: 2;
		@include md {
			display: none; }
		.hero__inner {
			justify-content: space-between;
			display: flex; } }
	&__counter {
		@include f(16px,800,1.5);
		color: $white;
		@include ftitle;
		span {
			opacity: .5; } } }
