.services {
	padding-bottom: 100px;
	@include sm {
		padding-bottom: 0; }
	&__titlelg {
		margin-bottom: 70px;
		@include sm {
			margin-bottom: 62px; } } }
.service {
	display: flex;
	@include sm {
		position: relative;
		padding: 70px 0;
		display: block; }
	&:not(:last-child) {
		margin-bottom: 70px;
		@include sm {
			margin-bottom: 0; } }
	&__img {
		flex-grow: 1;
		@include md {
			margin-right: 0; }
		@include sm {
			position: absolute;
			top: 0;
			left: -37px;
			right: -37px;
			bottom: 0;
			margin: {} }
		& + .service__content {
			padding-right: 0;
			padding-left: 97px;
			@include md {
				padding: 0 0 0 30px; } }
		img {
			max-width: 100%;
			display: block;
			@include sm {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: left;
				opacity: .7; } } }
	&__content {
		position: relative;
		padding: 70px 80px 0 0;
		width: 40%;
		flex-shrink: 0;
		z-index: 1;
		@include md {
			padding: 70px 30px 0 0;
			width: 50%; }
		@include sm {
			padding: 0;
			width: 100%; }
		& + .service__img {
			@include md {
				margin-left: 0; }
			@include sm {
				margin: 0; } } }
	&__title {
		margin-bottom: 30px; }
	&__text {
		max-width: 330px;
		@include sm {
			max-width: 300px; } }
	&__footer {
		margin-top: 62px; } }
