body {
	position: relative;
	@include fmain;
	@include f(14px,400,1.71);
	color: rgba($white,.7);
	@include font-smoothing;
	min-width: 375px;
	background-color: $black;
	.is-overflow-hidden & {
		position: fixed;
		width: 100%;
		height: 100%; } }

.titlelg,
.title,
.titlesm,
.titlexs,
.label,
.town,
.street {
	@include ftitle;
	color: $white;
	.color-black & {
		color: $black; }
	&.color-white {
		color: $white;
		.color-black & {
			color: $white; } } }
.color-black {
	color: rgba($black, .7);
	.color-white & {
		color: rgba($black, .7); } }
.color-white {
	color: $white;
	.color-black & {
		color: $white; } }

.titlelg {
	@include f(75px,700,1);
	span {
		display: block;
		.inline {
			display: inline; } }
	@include md {
		font-size: 52px; } }

.title {
	@include f(52px,700,1);
	span {
		display: block;
		&.inline {
			display: inline; } } }

.titlesm {
	@include f(32px,800,1.1875);
	span {
		display: block;
		&.inline {
			display: inline; } } }

.titlexs {
	@include f(24px,800,1.5); }

.colorblue {
	color: $blue; }

.text-center {
	text-align: center; }
.text-right {
	text-align: right; }

.inline {
	display: inline; }

.label {
	@include f(14px,400,1.5); }
.town {
	@include f(16px,800,1.5);
	@include sm {
		br {
			display: none; } } }
.street {
	@include f(16px,400,1.5);
	color: rgba($white, .5);
	.color-black & {
		color: rgba($black, .5); }
	@include sm {
		br {
			display: none; } } }

.soc {
	flex-wrap: wrap;
	display: flex;
	li {
		&:not(:last-child) {
			margin-right: 40px;
			.soc_lg & {
				margin-right: 28px; } } }
	a {
		color: $white;
		font-size: 16px;
		@include tr(opacity);
		line-height: 1;
		display: block;
		@include hover {
			opacity: .5; } }
	&_lg {
		li {
			&:not(:last-child) {
				margin-right: 28px; } }
		a {
			font-size: 24px; } } }
