.btn,
.btn-simple {
	padding: 18px 31px;
	@include ftitle;
	@include f(16px,700,1.5);
	letter-spacing: 0.1em;
	@include tr(color,background-color);
	display: inline-block;
	vertical-align: top;
	&:disabled {
		cursor: default;
		background-color: #C7C7C7; }
	&_sm {
		padding: 8px 22px;
		letter-spacing: 0;
		font-weight: 800; } }

.btn {
	color: $white;
	background-color: $blue;
	&:not(.btn_sm) {
		text-transform: uppercase; }
	&:not(:disabled) {
		@include hover {
			color: $blue;
			background-color: #F9F9F9; } }
	&_white {
		color: $blue;
		background-color: $white;
		&:not(:disabled) {
			@include hover {
				background-color: #F9F9F9; } } } }

.btn-simple {
	padding-left: 0;
	padding-right: 0;
	color: $blue;
	@include md {
		padding: 0; }
	&:not(:disabled) {
		@include hover {
			color: $white; } } }
