.offers {
	&__title {
		margin-bottom: 20px; }
	&__row {
		position: relative;
		margin: 0;
		counter-reset: list;
		&:not(:first-child) {
			margin-top: 70px; } }
	&__col {
		position: relative;
		padding: 44px 50px 100px;
		width: 33.3%;
		justify-content: space-between;
		flex-direction: column;
		display: flex;
		@include sm {
			padding: 70px 37px;
			width: 100%; }
		@include hover {
			.offers__btn {
				opacity: 0;
				visibility: hidden; }
			.offers__btnonhover {
				opacity: 1;
				visibility: visible; }
			.offers__img {
				opacity: 1;
				visibility: visible; } } }
	&__wrap {
		position: relative;
		z-index: 2;
		&:before {
			margin-bottom: 30px;
			counter-increment: list;
			content: '0'counter(list);
			@include f(32px,800,1.1875);
			opacity: .2;
			@include ftitle;
			display: block; } }
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		@include tr(opacity, visibility);
		background-color: $blue;
		@include md {
			display: none; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			display: block;
			opacity: .12; } }
	&__titlesm {
		margin-bottom: 30px; }
	&__text {
		margin-bottom: 18px;
		@include f(10px,700,1.3);
		color: $white;
		letter-spacing: 0.1em;
		text-transform: uppercase; }
	&__list {
		line-height: 1.28;
		li {
			position: relative;
			padding-left: 10px;
			display: block;
			&:before {
				position: absolute;
				content: '';
				top: 7px;
				left: 0;
				@include circle(3);
				background-color: $white; }
			&:not(:last-child) {
				margin-bottom: 15px; } } }
	&__footer {
		position: relative;
		margin-top: 50px;
		z-index: 2; }
	&__btn {
		@include tr(opacity,visibility); }
	&__btnonhover {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		@include tr(opacity,visibility);
		opacity: 0;
		visibility: hidden;
		@include md {
			visibility: visible;
			opacity: 1; } } }
