// GENERATE GRID EXAMPLE
// CUSTOM PADDING ON BREAKPOINTS - NOT INCLUDED IN MIXIN
@include generate_grid($columns: 12, $sizeXs: false, $sizeSm: true, $sizeMd: true, $sizeLg: true, $padding: 15px);

.out {
	position: relative;
	overflow: hidden; }

.inner {
	padding: 0 15px;
	margin: 0 auto;
	max-width: 1140px;
	@include md {
		padding: 0 37px; } }

.wrap {
	padding-top: 249px;
	@include md {
		padding-top: 150px; }
	@include sm {
		padding-top: 114px; } }

.whitewrap {
	margin-top: 179px;
	padding: 70px 0 100px;
	background-color: #F4F4F4;
	@include md {
		margin-top: 100px;
		padding: 40px 0 70px; } }
