.packages {
	&__inner {
		@include md {
			padding: 0; } }
	&__row {
		counter-reset: list;
		margin: 0;
		@include md {
			margin: 0; } }
	&__col {
		position: relative;
		width: 33.3%;
		padding: 60px 50px 100px;
		justify-content: space-between;
		flex-direction: column;
		display: flex;
		@include md {
			padding: 70px 37px; }
		@include sm {
			width: 100%; }
		@include hover {
			.packages__btn {
				opacity: 0;
				visibility: hidden; }
			.packages__btnonhover {
				opacity: 1;
				visibility: visible; }
			.packages__img {
				opacity: 1;
				background-color: $blue; } } }

	&__btn {
		@include tr(opacity,visibility); }
	&__btnonhover {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		@include tr(opacity,visibility);
		opacity: 0;
		visibility: hidden; }
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		@include tr(background-color, opacity);
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			display: block;
			opacity: .2; } }
	&__wrap {
		position: relative;
		z-index: 2;
		&:before {
			margin-bottom: 30px;
			counter-increment: list;
			content: '0'counter(list);
			@include f(32px,800,1.1875);
			opacity: .2;
			@include ftitle;
			display: block; } }
	&__title {
		margin-bottom: 30px; }
	&__footer {
		position: relative;
		z-index: 2;
		margin-top: 52px; } }
