.ourclients {
	padding: 93px 0 60px;
	@include sm {
		padding: 70px 0 0; }
	&__title {
		margin-bottom: 70px; }
	&__col {
		margin-bottom: 40px;
		@include sm {
			margin-bottom: 0;
			justify-content: center;
			display: flex; }
		&:nth-child(even) {
			justify-content: flex-end;
			display: flex;
			@include sm {
				justify-content: center; } } }
	&__fitnesscoach {
		@include sm {
			margin: 0 -37px; } } }
